*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
:root{
    font-size: 10px;
    --primary-color: hsl(0, 0%, 90%);
    --secondary-color: hsl(0, 0%, 84%);
    --surface-color: hsl(0, 0%, 12%);
    --dark-surface-color: hsl(0, 0%, 7%);
    --bg-color: hsl(0, 0%, 0%);
    --odd-color: hsl(0, 51%, 51%);
}

body{
    background-color: var(--bg-color);
    color: var(--primary-color);
    min-height: 100vh;
}

a,p,li,input, button{
    font-size: 1.7rem;
}

a, p{
    font-size: 1.7rem;
    color: var(--primary-color);
}

a{
    text-decoration: none;
}

h1, h2, h3{
    color: var(--secondary-color);
}

h1{
    font-size: 2.4rem;
}

h2{
    font-size: 2.2rem;
}

h3{
  font-size: 2rem;  
}

ul{
    list-style-type: none;
}

main{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    flex-direction: column;
}

main> h1{
    font-size: 3rem;
}

.container{
    border-radius: 2rem;
    min-width: 70rem;
    margin: 2rem auto 0 auto;
    background-color: var(--dark-surface-color);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 80vh;
    padding: 1.5rem;
    position: relative;
}

@media screen and (max-width: 730px) {
    .container{
        min-width: 40rem;
    }
}

@media screen and (max-width: 400px) {
    main{
        padding: 1rem;
    }
    main>h1{
        text-align: center;
    }
    .container{
        min-width: 28rem;
        margin: 1rem auto 0 auto;
    }
}