
.messageArea{
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 90%;
    overflow-y: scroll;
    padding: 1rem;
}

.inputArea{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 8%;
}

.inputArea input{
    width: 92%;
    padding: 0.7rem 2rem;
    border: none;
    height: 90%;
    border-radius: 7rem;
    margin: 0 0.6rem 0 0;
}

.inputArea input:focus{
    outline: none;
    background-color: var(--secondary-color);
}

.inputArea svg{
    width: 5.5rem;
    height: 5rem;
    padding: 1rem;
    border-radius: 50%;
    background-color: var(--odd-color);
}
.message{
    background-color: var(--surface-color);
    padding: 0.7rem;
    
    margin: 0.2rem 0 ;
    position: relative;
}
.small{
    font-size: 1rem;
    line-height: 0.8rem;
    color: rgb(239, 108, 108);
}
.samosa{
    width: 0;
	height: 0;
	border-left: 0rem solid transparent;
	border-right: 1rem solid transparent;
	border-bottom: 1rem solid var(--surface-color);
    top: 0;
    position: absolute;

}

.rightSamosa{
    right: -0.5rem;
    transform: rotate(90deg);
}
.leftSamosa{
    left: -0.5rem;
    transform: rotate(180deg);
}

.right{
    align-self: flex-end;
    border-radius: 0.3rem 0 0.3rem 0.3rem ;
}

.left{
    align-self: flex-start;
    border-radius: 0 0.3rem 0.3rem 0.3rem;
}
.joinedUser{
    background-color: var(--odd-color);
    position: absolute;
    top: 1rem;
    z-index: 99;
    border-radius: 0.2rem;
    padding: 0.5rem 2rem;
}
::-webkit-scrollbar {
    width: 3px;
}

::-webkit-scrollbar-track {
    background-color: transparent;

  }
   
  ::-webkit-scrollbar-thumb {
    background: var(--secondary-color); 
    height: 10px;
    border-radius: 2rem;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background-color: white;
  } 
  