
.logoCon>h1{
    font-size: 6rem;
    margin: -6rem  0 0 7rem;
    user-select: none;
}
.container .logoCon{
    height: 90%;
}

.logoCon svg{
    height: 25rem;
    width: 25rem;
}
.inputArea button{
    padding: 0.9rem 1rem;
    border-radius: 3rem;
    border: none;
    font-weight: 600;
    transition: 0.3s all;
}
.inputArea button:hover{
    background-color: var(--bg-color);
    color: white;
    cursor: pointer;
}